$desktop-width: 1136px;
$tablet-width: 768px;
$mobile-width: 480px;

@import "null.scss";
@import "fonts.scss";
@import "visually-hidden.scss";

body {
    height: 100%;
    
}
.wrapper {
    height: 100%;
    position: relative;

    background: radial-gradient(152.33% 152.33% at 50% 50%, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%), #F3D4C3;
    
}

.background {
    width: 100%;
    height: 100%;
    position: absolute;

    &:before {
        content: '';
        position: absolute;
        width: 50%;
        height: 100%;
        top: 0;
        left: 0;

        background: url(../img/background-2.jpg) 50.5% 0;
        background-size: auto 1760px;
        opacity: 0.2;
    }

    &:after {
        content: '';
        position: absolute;
        width: 50%;
        height: 100%;
        top: 0;
        right: 0;

        background: url(../img/background-1.jpg) 50.5% 0;
        background-size: auto 1760px;
        opacity: 0.2;
    }
}

.content {
    margin: 0 auto;
    padding: 25px 0 72px 0;
    max-width: 320px;
    position: relative;

    &:before {
        content: '';
        position: absolute;
        display: block;
        width: 100%;
        height: 100%;
        background: #FFFFFF;
        border-radius: 50%;
        filter: blur(400px);
    }
}

// Description

.description {
    margin: 0;
    padding: 0;
}

.pageLogo-wrapper {
    margin: 0 auto;
    width: 306px;
    height: 80px;

    position: relative;

    &:before {
        content: '';
        position: absolute;
        display: block;
        width: 100%;
        height: 100%;
        background: #FFFFFF;
        border-radius: 50%;
        filter: blur(20px);
    }

    * {
        filter: blur(0px);
    }

    +* {
        filter: blur(0px);
    }
}

.pageLogo {
    margin: 0 auto;
}

.description-title {
    margin: 0 auto;
    padding: 1px 4px 0 0;
    max-width: 255px;

    font-family: Inter, Helvetica, sans-serif;
    font-style: normal;
    text-align: center;
    font-size: 32px;
    line-height: 40px;
    font-weight: 900;
    text-transform: uppercase;
    color: #118976;
    text-shadow: 2px 2px 0px #FFFFFF;
}

.advantageList {
    margin: 32px auto 0 auto;
    padding: 0;

    width: 300px;
}

.advantageList-item {
    margin: 0 auto;
    padding: 0 44px 0 76px;
    position: relative;

    font-family: Inter, Helvetica, sans-serif;
    font-style: normal;
    text-align: left;
    font-size: 18px;
    line-height: 24px;
    font-weight: 700;
    text-transform: none;
    color: #333333;
    text-shadow: none;

    +.advantageList-item {
        margin-top: 20px;
    }

    &:before{
        content: '';
        width: 56px;
        height: 56px;
        top: 6px;
        left: 0;

        position: absolute;
        background: url(../img/iconChecked.png);
    }
}

// product-image

.product-image {
    margin: 39px auto 0 auto;
    padding: 0 0 0 8px;
    height: 388px;

    position: relative;
}

// Order

.order {
    margin: 74px auto 0 auto;
    padding: 24px 32px;
    max-width: 320px;

    position: relative;

    background-color: #FFFFFF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 2px;
}

.order__title {   
    font-family: Inter, Helvetica, sans-serif;
    font-style: normal;
    text-align: center;
    font-size: 28px;
    line-height: 32px;
    font-weight: 700;
    text-transform: none;
    color: #333333;
    letter-spacing: -0.01em;
    text-shadow: none;
}

.order__offer {
    padding: 46px 0 0 12px;

    font-family: Inter, Helvetica, sans-serif;
    font-style: normal;
    text-align: center;
    font-size: 22px;
    line-height: 22px;
    font-weight: 700;
    text-transform: none;
    color: #FF2C2C;
    letter-spacing: -0.017em;
    text-shadow: none;
}

.orderForm__offerPrice {
    padding: 8px 0 0 18px;

    font-family: Inter, Helvetica, sans-serif;
    font-style: normal;
    text-align: center;
    font-size: 38px;
    line-height: 42px;
    font-weight: 800;
    text-transform: none;
    color: #FF2C2C;
    letter-spacing: 0.05em;
    text-shadow: none;
}

// order__form

.order__form {
    margin: 45px 0 0 0;
    max-width: 254px;
}

.order__form-label {
    position: relative;
}

.order__form-labelArrow {
    content: '';
    width: 9px;
    height: 9px;
    position: absolute;
    top: 3px;
    right: 22px;

    border-right: 2px solid #333333;
    border-bottom: 2px solid #333333;
    transform: rotate(45deg);
}

.order__form-item {
    margin: 12px 0 0 0;
    padding: 0 0 0 18px;
    width: 100%;
    height: 42px;

    border: 1px solid #089C84;
    border-radius: 2px;

    font-family: Inter, Helvetica, sans-serif;
    font-style: normal;
    text-align: left;
    font-size: 14px;
    line-height: 1.17em;
    font-weight: 400;
    text-transform: none;
    color: #333333;
    text-shadow: none;

    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;

    &::-ms-expand {
        display: none;
    }

    &:focus {
        outline: 1px solid rgba(20, 216, 183, 0.773);
    }

    &:active {
        outline: none;
    }
}

.order__button {
    cursor: pointer;
    margin: 20px auto 0 auto;
    padding: 18px 76px 20px 79px;
    display: block;

    font-family: Inter, Helvetica, sans-serif;
    font-style: normal;
    text-align: center;
    font-size: 18px;
    line-height: 1em;
    font-weight: 700;
    text-transform: uppercase;
    color: #FFFFFF;
    text-shadow: none;

    background-color: #FF272B;
    box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.25);
    border: none;
    border-radius: 50px;

    &:focus {
        outline: 1px solid #089C84;
    }

    &:active {
        outline: none;
        opacity: 0.8;
    }
}

.order__footnote {
    margin: 16px 0 0 -1px;
    font-family: Inter, Helvetica, sans-serif;
    font-style: normal;
    text-align: center;
    font-size: 12px;
    line-height: 20px;
    font-weight: 400;
    text-transform: none;
    color: #828282;
    text-shadow: none;
}


