@font-face {
    font-family: Inter; /* Имя шрифта */
    src: url(../fonts/Inter/Inter400.eot); 
    src: url(../fonts/Inter/Inter400.ttf) format('truetype'),
    url(../fonts/Inter/Inter400.woff) format('woff'),
    url(../fonts/Inter/Inter400.svg) format('svg');
    font-weight: 400;
    font-style: normal;
}

